.logo-img{
    height: 42px;
    text-align: center;
}
.banner{
    background-image: url(../images/bg_home.jpg);
    background-position:center;
    background-size: cover;
    height: 90vh;
}
.double-color{
    background: linear-gradient(to top, #62DB54, #65dbdd, #09ACF5);
    background: -webkit-linear-gradient(to top, #62DB54, #65dbdd, #09ACF5);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
}
